<!-- Use preprocessors via the lang attribute! e.g. <template lang="pug"> -->
<template>
  <div id="app" class="container mt-3 p-3 mb-5 rounded">
    <div id="yoda-icon"></div>
    <h1 class="text-uppercase" id="title">Yodafi</h1>
    <div class="row">
      <div class="col-md mb-3">
        <div class="input-group input-group-lg">
          <input
            type="text"
            class="form-control text-center"
            aria-label="Large"
            aria-describedby="inputGroup-sizing-sm"
            v-model="textBox"
          />
        </div>
      </div>
      <div class="col-md">
        <div class="alert alert-success text-lowercase" role="alert">
          <h5 class="alert-heading mb-0">{{ yodaized }}</h5>
        </div>
      </div>

      <!--     <p>
      Yodafi any sentence to yoda language.
    </p> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      textBox: "you can yodafi any sentence.",
      scrollPosition: 0,
    };
  },
  mounted() {
    // this.yodifi();
    console.log(this.scrollPosition++);
    let scrollPos = this.scrollPosition;
    let scroll = function () {
      scrollPos++;
      document.getElementById(
        "yoda-icon"
      ).style.backgroundPosition = `${scrollPos}px`;
      window.requestAnimationFrame(scroll);
    };
    window.requestAnimationFrame(scroll);
  },
  computed: {
    yodaized() {
      let sentence = this.textBox.split(" ");
      let firstWords = `${sentence[0]} ${sentence[1]}`;
      sentence.splice(0, 2);
      sentence = sentence.join(" ");
      let yodaize = "";
      let lastChar = sentence[sentence.length - 1];
      if (lastChar == "!" || lastChar == "?" || lastChar == ".") {
        sentence = sentence.slice(0, -1);
        yodaize += sentence;
        yodaize += ` ${firstWords}`;
        yodaize += lastChar;
      } else {
        yodaize += sentence;
        yodaize += ` ${firstWords}`;
      }
      return yodaize;
    },
  },
  methods: {
    yodifi() {
      let sentence = this.textBox.split(" ");
      let firstWords = `${sentence[0]} ${sentence[1]}`;
      sentence.splice(0, 2);
      sentence = sentence.join(" ");
      let yodaize = "";
      let lastChar = sentence[sentence.length - 1];
      if (lastChar == "!" || lastChar == "?" || lastChar == ".") {
        sentence = sentence.slice(0, -1);
        yodaize += sentence;
        yodaize += ` ${firstWords}`;
        yodaize += lastChar;
      } else {
        yodaize += sentence;
        yodaize += ` ${firstWords}`;
      }
      this.yodized = yodaize;
    },
  },
};
</script>

<!-- Use preprocessors via the lang attribute! e.g. <style lang="scss"> -->
<style>
@import url(https://fonts.googleapis.com/css?family=Bungee);
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 0 55px MediumAquamarine;
}
#yoda-icon {
  background-image: url("https://zachdyer.com/images/yodafiah/yoda_icon.png");
  height: 250px;
  margin: 0 auto;
  background-position: center;
}
#title {
  font-family: "Bungee";
  color: MediumAquamarine;
}
html,
body {
  background-color: rgb(37, 37, 37);
  height: 100%;
}
body {
  display: flex;
  align-items: center;
}
</style>
